import { Box, Button, Typography, makeStyles } from "@material-ui/core";
import { AccessTime, ArrowForward } from "@material-ui/icons";
import KSActionService from "../../../services/KSActionService";
import { useSearchParams } from "react-router-dom";
import KatalonTour from "../../../variables/KatalonTour";

const useStyles = makeStyles({
  runButton: {
    backgroundColor: "#000",
    color: "#fff",
    textTransform: "inherit",
    fontSize: "12px",
    fontFamily: "Inter",
    lineHeight: "24px",
    fontWeight: "600",
    minWidth: "140px",
    "&:hover": {
      backgroundColor: "#424242",
    },
    "&.MuiButton-root": {
      padding: "6px 0px",
    },
  },
  icon: {
    fontSize: "12px",
    lineHeight: "12px",
  },
});

export const DescriptionText = ({ isOpenedProject, completedTours, activateTour }) => {
  const [url, setUrl] = useSearchParams();
  const tourType = url.get("tour_type");

  const classes = useStyles();

  const buttonName =
    completedTours && completedTours.includes(activateTour.id)
      ? "Replay"
      : activateTour.action;

  function handleStartTour() {
    KSActionService.startTour(activateTour.id);
  }

  const isDisableTour = () => {
    // Not open project
    if (!isOpenedProject) {
      return true;
    }
    // WebUI, API, Mobile, Desktop Tour
    if (activateTour.id === KatalonTour.CREATE_FIRST_PROJECT) {
      return true;
    }
    // For CREATE_FIRST_PROJECT ON STARTPAGE
    if (
      activateTour.id === KatalonTour.WEB_TESTING &&
      tourType === KatalonTour.CREATE_FIRST_PROJECT
    ) {
      return (
        completedTours &&
        completedTours.includes(KatalonTour.CREATE_FIRST_PROJECT)
      );
    }
    if (activateTour.id === tourType) {
      return (
        completedTours &&
        completedTours.includes(KatalonTour.CREATE_FIRST_PROJECT)
      );
    }
    // BDD
    if (activateTour.id === KatalonTour.BDD_TESTING_FEATURE_FILE) {
      return true;
    }
    if (activateTour.id === KatalonTour.BDD_TESTING_STEP_DEFINITION) {
      return (
        completedTours &&
        completedTours.includes(KatalonTour.BDD_TESTING_FEATURE_FILE)
      );
    }
    if (activateTour.id === KatalonTour.BDD_TESTING_ADDING_TO_TEST_CASE) {
      return (
        completedTours &&
        completedTours.includes(KatalonTour.BDD_TESTING_STEP_DEFINITION)
      );
    }
    // TS, Analyze, TC, KRE
    if (activateTour.id === KatalonTour.TEST_PLANNING) {
      return (
        completedTours &&
        (completedTours.includes(KatalonTour.API_TESTING) ||
          completedTours.includes(KatalonTour.WEB_TESTING) ||
          completedTours.includes(KatalonTour.MOBILE_TESTING) ||
          completedTours.includes(KatalonTour.DESKTOP_TESTING) ||
          completedTours.includes(KatalonTour.BDD_TESTING) ||
          completedTours.includes(KatalonTour.GENERIC_TESTING_TESTING) ||
          completedTours.includes(KatalonTour.BDD_TESTING_ADDING_TO_TEST_CASE))
      );
    }
    if (
      activateTour.id === KatalonTour.ANALYZE_TEST_ACTIVITIES ||
      activateTour.id === KatalonTour.TEST_CLOUD_TESTING ||
      activateTour.id === KatalonTour.SET_UP_KRE
    ) {
      return (
        completedTours && completedTours.includes(KatalonTour.TEST_PLANNING)
      );
    }
    return false;
  };

  return (
    <Box
      style={{
        display: "flex",
        flexDirection: "column",
        padding: "16px 0px 16px 0px",
        width: "50%",
      }}
    >
      <Box>
        <Typography
          style={{
            fontWeight: 400,
            fontSize: "0.875rem",
          }}
          dangerouslySetInnerHTML={{
            __html: activateTour.description,
          }}
        ></Typography>
      </Box>
      <Box style={{ display: "flex", alignItems: "center", marginTop: "5px" }}>
        <AccessTime style={{ fontSize: "0.875rem", marginRight: "10px" }} />
        <Typography
          style={{ fontSize: "0.875rem", marginRight: "10px" }}
          component="p"
        >
          ETA
        </Typography>
        <div
          style={{
            borderRight: "1px solid gray",
            height: "14px",
            marginRight: "10px",
          }}
        />
        <Typography style={{ fontSize: "0.875rem", marginRight: "10px" }}>
          {activateTour.estimateTime}m
        </Typography>
      </Box>
      <Box style={{ display: "flex", alignItems: "center", marginTop: "5px" }}>
        <Button
          disabled={!isDisableTour()}
          onClick={handleStartTour}
          variant="contained"
          className={classes.runButton}
          disableElevation
        >
          {buttonName}
          <ArrowForward style={{ fontSize: "1rem", marginLeft: "5px" }} />
        </Button>
      </Box>
    </Box>
  );
};
