import { Grid } from "@material-ui/core";
import { useSearchParams } from "react-router-dom";
import React from "react";
import KatalonTours from "../../../variables/KatalonTours";
import { StepList } from "./StepList";
import KatalonTour from "../../../variables/KatalonTour";

export const QuickStartCheckList = () => {
  const [url, setUrl] = useSearchParams();
  var tourType = url.get("tour_type");

  if (!tourType || tourType === KatalonTour.CREATE_FIRST_PROJECT) {
    tourType = KatalonTour.WEB_TESTING;
  }
  const selectedTours = KatalonTours[tourType];

  return (
    <Grid
      container
      spacing={4}
      justifyContent="center"
      alignItems="stretch"
    >
      <StepList selectedTours={selectedTours} tourType={tourType} />
    </Grid>
  );
};
