import {
  Button,
  Grid,
  ThemeProvider,
  Typography,
  createTheme,
  makeStyles,
} from "@material-ui/core";
import KatalonLogo from "../../../../assets/icon/Katalon-Logo.svg";
import CongratulationV2Image from "../../../../assets/img/congratulation-v2.svg";
import { ArrowBack } from "@material-ui/icons";
import KatalonTours from "../../../variables/KatalonTours";

const textTheme = createTheme({
  typography: {
    fontFamily: "Inter",
  },
});

const useStyles = makeStyles({
  root: {
    animation: "fadeIn .5s ease-in-out",
    backgroundColor: "#FFFF",
    height: "100%",
    marginTop: "0",
    zoom: 1.2,
  },
  logo: {
    animation: "fadeIn 1s ease-in-out",
  },
  button: {
    animation: "fadeIn 1s ease-in-out",
    fontWeight: 600,
    textTransform: "none",
    color: "white",
    backgroundColor: "#000000",
    borderRadius: "4px",
    fontSize: "12px",
    width: "180px",
    height: "28px",
  },
});

export const CongratulationPageV2 = ({ btnHandler, tourType, tour }) => {
  const classes = useStyles();

  const btnText = "Back to Walkthrough";

  const msg1 = "You have learned to";
  const message1 = "Task finished, good job!";
  let message2;
  if (tourType !== "CREATE_FIRST_PROJECT") {
    const tourName = KatalonTours[tourType].find(
      (aTour) => aTour.id === tour
    ).title;
    const msg2 = tourName.charAt(0).toLowerCase() + tourName.slice(1);
    message2 = msg1 + " " + msg2 + ".";
  } else {
    message2 = msg1 + " " + "create your first project.";
  }

  return (
    <ThemeProvider theme={textTheme}>
      <Grid
        container
        align="center"
        className={classes.root}
        alignContent="center"
        alignItems="center"
        justifyContent="center"
        spacing={4}
      >
        <Grid item xs={12}>
          <img src={KatalonLogo} alt="Katalon Logo" className={classes.logo} />
        </Grid>
        <Grid item xs={12}>
          <img src={CongratulationV2Image} alt="Congratulation" />
        </Grid>
        <Grid item xs={12}>
          <Typography
            style={{ fontSize: "28px", fontWeight: 600, color: "#22283C" }}
          >
            {message1}
          </Typography>
        </Grid>
        <Grid item xs={12}>
          <Typography
            style={{ fontSize: "16px", fontWeight: 500, color: "#4F5465" }}
          >
            {message2}
          </Typography>
        </Grid>
        <Grid item xs={12}>
          <Button
            variant="contained"
            size="small"
            color="primary"
            disableElevation
            onClick={btnHandler}
            className={classes.button}
          >
            <ArrowBack
              style={{ fontSize: "0.8rem", marginRight: 4 }}
            ></ArrowBack>
            {btnText}
          </Button>
        </Grid>
      </Grid>
    </ThemeProvider>
  );
};
