import axios from 'axios';
import KatalonAction from '../variables/KatalonAction';
import KatalonEndpoints from '../variables/KatalonEndpoints';

export default class KSActionService {
  static async do(action, params = {}) {
    const query = Object.entries(params).map(([key, value]) => `${key}=${encodeURIComponent(value)}`).join('&');
    const queryString = query ? `&${query}` : '';
    return await axios.post(`${KatalonEndpoints.KatalonAction}?action=${action}${queryString}`, {})
  }

  static async sendFeedback(action, num_stars, feedback_text, params = {}) {
    const query = Object.entries(params).map(([key, value]) => `${key}=${encodeURIComponent(value)}`).join('&');
    const queryString = query ? `&${query}` : '';
    return await axios.post(`${KatalonEndpoints.KatalonAction}?action=${action}${queryString}`, {feedback: feedback_text, numOfStars: num_stars});
  }

  static async checkForUpdate() {
    return await this.do(KatalonAction.CheckForUpdate);
  }

  static async reviewBestPractice(url, review) {
    return await this.do(KatalonAction.ReviewBestPractice, { url, review });
  }

  static async startTour(id) {
    return await this.do(KatalonAction.StartTour, { id });
  }

  static async endKRETour() {
    return await this.do(KatalonAction.EndKRETour);
  }

  static async learnMoreAboutCICD() {
    return await this.do(KatalonAction.LearnMoreAboutCICD);
  }

  static async clickIGotItBtn() {
    return await this.do(KatalonAction.IGotItBtnClicked);
  }

  static async openKatalonStudioWalkthrough() {
    return await this.do(KatalonAction.OpenStudioWalkthrough);
  }

  static async openKatalonPlatformWalkthrough() {
    return await this.do(KatalonAction.OpenPlatformWalkthrough);
  }

  static async openIntegrateWithPlatform() {
    await this.do(KatalonAction.IntegrateWithPlatform);
  }

  static async sendStudioAssistFeedback(num_stars, feedback_text) {
    await this.sendFeedback(KatalonAction.SendStudioAssistFeedback, num_stars, feedback_text);
  }
}
