import { Button, Grid, makeStyles, Typography } from "@material-ui/core";
import React from "react";
import KSActionService from "../../../services/KSActionService";
import KatalonTour from "../../../variables/KatalonTour";
/* eslint-disable import/first */
import { QuickStartCheckList } from "./QuickStartCheckList";
import { Scrollbars } from "react-custom-scrollbars";
import KatalonLogo from "../../../../assets/icon/Katalon-Logo.svg";
import { CongratulationPageV2 } from "./CongratulationPageV2";

const useStyles = makeStyles({
  root: {
    background: `#fff`,
    height: "100%",
    marginTop: "0",
    backgroundColor: "#fff",
    zoom: "120%",
  },
  WindowsWidth: {
    height: "100%",
    minWidth: "1400px",
  },
  macOSWidth: {
    height: "100%",
    minWidth: "750px",
  },
  skipButton: {
    textTransform: "initial",
    fontWeight: "normal",
    marginTop: "40px",
  },
  gettingStarted: {
    fontFamily: "Inter",
    fontSize: "28px",
    fontWeight: 600,
    color: "#22283C",
    lineHeight: "36px",
    height: "36px",
    width: "217px",
  },
  welcomeMessage: {
    marginTop: "20px",
    fontFamily: "Inter",
    fontSize: "16px",
    color: "#4F5465",
    fontWeight: 500,
    lineHeight: "28px",
  },
  helpGrid: {
    marginTop: "40px",
  },
  helpMessage: {
    fontFamily: "Inter",
    fontWeight: 500,
    fontSize: "12px",
    lineHeight: "28px",
    color: "#4F5465",
  },
  logo: {
    width: "108.5px",
    height: "28px",
    marginBottom: "30px",
  },
});

export const QuickStartOverviewContentV2 = () => {
  const classes = useStyles();
  const url = new URLSearchParams(window.location.search);
  const tourType = url.get("tour_type");
  const tour = url.get("prev_tour");
  const ignoreCongratulation = url.get("ignore_congratulation") ?? false;
  const [showCongratulation, setShowCongratulation] = React.useState(
    !!tour && !ignoreCongratulation
  );

  const isWindows = navigator.platform.includes('Win')

  function handleGoBack() {
    setShowCongratulation(false);
  }

  function handleSkipTour() {
    KSActionService.startTour(KatalonTour.SKIP_TOUR);
  }

  document.body.style.background = "white";
  return showCongratulation ? (
    <CongratulationPageV2 btnHandler={handleGoBack} tourType={tourType} tour={tour}/>
  ) : (
    <Scrollbars
      autoHide
      hideTracksWhenNotNeeded
      style={{ width: "100%" }}
      autoHeightMin="100%"
      autoHeightMax="100%"
    >
      <Grid
        container
        spacing={4}
        justifyContent="center"
        alignItems="center"
        alignContent="center"
        className={classes.root || isWindows ? classes.WindowsWidth : classes.macOSWidth}
      >
        <Grid item xs={10} sm={10}>
          <Grid
            item
            container
            justifyContent="center"
            alignItems="center"
            spacing={4}
            className={classes.content}
          >
            <Grid item xs={10} sm={12} align="center">
              <img
                src={KatalonLogo}
                className={classes.logo}
                alt={"Katalon_Logo"}
              />
              <Typography
                align="center"
                variant="h4"
                className={classes.gettingStarted}
              >
                Getting Started
              </Typography>
              <Typography
                align="center"
                variant="h4"
                className={classes.welcomeMessage}
              >
                The fastest way to explore the full potential of Katalon
              </Typography>
            </Grid>
            <Grid item xs={10} sm={9}>
              <QuickStartCheckList />
            </Grid>
          </Grid>
        </Grid>
        <Grid item container xs={10} justify="center">
          <Button
            color="primary"
            size="small"
            onClick={handleSkipTour}
            className={classes.skipButton}
          >
            Maybe Later
          </Button>
        </Grid>
      </Grid>
    </Scrollbars>
  );
};
