import { createBrowserRouter, RouterProvider } from "react-router-dom";
import { lazy } from "react";
import { CongratulationPageV2 } from "./client/pages/quick-start/components/CongratulationPageV2";
import { QuickStartOverviewContentV2 } from "./client/pages/quick-start/components/QuickStartOverviewContentV2";

const CreativeStartPage = lazy(
  () => import("./client/pages/start-page/CreativeStartPage")
);
const EmailTemplatePage = lazy(
  () => import("./client/pages/email-template/EmailTemplatePage")
);
const MultiMediaPage = lazy(
  () => import("./client/pages/multi-media/MultiMediaPage")
);
const QuickStartPage = lazy(
  () => import("./client/pages/quick-start/QuickStartPage")
);
const StartPage = lazy(() => import("./client/pages/start-page/StartPage"));

const TestSuiteAnalyticsPage = lazy(() => import("./client/pages/test-suite-analytics/TestSuiteAnalytics"));

const StartPageV2 = lazy(() => import("./client/pages/start-page-v2/StartPage"));

const KREExecutionInstruction = lazy(() => import("./client/pages/kre-execution-instruction/KREExecutionInstruction"));

const ShareYourFeedback = lazy(() => import("./client/pages/studioassist/ShareYourFeedback"));

const router = createBrowserRouter([
  {
    path: "/CreativeStartPage.html",
    element: <CreativeStartPage />,
  },
  {
    path: "/EmailTemplatePage.html",
    element: <EmailTemplatePage />,
  },
  {
    path: "/MultiMedia.html",
    element: <MultiMediaPage />,
  },
  {
    path: "/QuickStartOverviewPage.html",
    element: <QuickStartOverviewContentV2 />,
  },
  {
    path: "/QuickStartPage.html",
    element: <QuickStartPage />,
  },
  {
    path: "/StartPage.html",
    element: <StartPage />,
  },
  {
    path: "/TestSuiteAnalytics.html",
    element: <TestSuiteAnalyticsPage />,
  },
  {
    path: "/KREExecutionInstruction.html",
    element: <KREExecutionInstruction />,
  },
  {
    path: "/ShareYourFeedback.html",
    element: <ShareYourFeedback />,
  }
]);

function App() {
  return <RouterProvider router={router} />;
}

export default App;
