import { Box } from "@material-ui/core";
import { DescriptionImage } from "./DescriptionImage";
import { DescriptionText } from "./DescriptionText";
import React from "react";

export const StepDescription = ({ isOpenedProject, completedTours, activateTour }) => {
  return (
    <Box
      style={{
        display: "flex",
        backgroundColor: activateTour.backgroundColor,
        width: 623,
        height: 294,
        borderRadius: "4px",
        paddingLeft: "32px",
        paddingTop: "29px",
        boxSizing: "border-box",
      }}
    >
      <DescriptionText
        isOpenedProject={isOpenedProject}
        completedTours={completedTours}
        activateTour={activateTour}
      />
      <DescriptionImage activateTour={activateTour} />
    </Box>
  );
};
