import * as React from "react";
import {
  Step,
  StepButton,
  Stepper,
  makeStyles,
  StepConnector,
  Divider,
  Grid,
} from "@material-ui/core";
import KSService from "../../../services/KSService";
import { StepDescription } from "./StepDescription";
import { useSearchParams } from "react-router-dom";

const styles = makeStyles({
  icon: {
    "& .MuiStepIcon-active": { color: "black" },
    "& .MuiStepIcon-completed": { color: "green" },
  },
  buttonLabel: {
    fontSize: "0.875rem",
    textAlign: "left",
    minHeight: "24px",
    padding: "0px",
    margin: "0px",
    color: "inherit",
    "& .MuiStepLabel-completed": {
      fontWeight: "initial",
    },
  },
  description: {},
  stepBox: {
    width: "283px",
    height: "294px",
  },
  connectorLine: {
    minHeight: "15px",
  },
  title: {
    maxWidth: "240px",
  }
});

export const StepList = ({ selectedTours, tourType }) => {
  const [url, setUrl] = useSearchParams();
  const [isOpenedProject, setIsOpenedProject] = React.useState(false);
  const [activateStep, setActivateStep] = React.useState(0);
  const [activateTour, setActivateTour] = React.useState(
    selectedTours[activateStep]
  );

  const targetTour = url.get("next_tour");

  const isWindows = navigator.platform.includes('Win');

  const rawCompletedTours = url.get("completed_tours");
  const [completedTours, setCompletedTours] = React.useState(
    rawCompletedTours && JSON.parse(rawCompletedTours)
  );

  const isForStartPage = () => {
    const paths = window.location.pathname.split("/");
    return paths.includes("StartPage.html");
  };

  const [displayCompletedTours, setDisplayCompletedTours] = React.useState();

  const handleStep = (index) => () => {
    const curTour = selectedTours[index];
    setActivateTour(selectedTours[index]);
    setActivateStep(index);
    handleChecklistDisplay(curTour);
  };

  const handleChecklistDisplay = (curTour) => {
    // Remove current tour is done to mark it is active Tour
    // And remark done when click other step
    if (!completedTours) {
      return;
    }
    const tempCompletedTours = [...completedTours];
    if (tempCompletedTours.includes(curTour.id)) {
      const isCompletedTour = tempCompletedTours.indexOf(curTour.id);
      tempCompletedTours.splice(isCompletedTour, 1);
    }
    setDisplayCompletedTours(tempCompletedTours);
  };

  const getTargetTour = () => {
    var index = 0;
    for (var tour of selectedTours) {
      if (tour.id === targetTour) {
        setActivateStep(index);
        setActivateTour(tour);
        break;
      }
      index++;
    }
  };

  const fetchIsOpenedProject = async () => {
    let data = await KSService.fetchiSOpenedProject();
    setIsOpenedProject(data);
  }

  const keepTrackTourProgress = () => {
    fetchIsOpenedProject();
    KSService.fetchUserProfile().then((profile) => {
      if (profile) {
        const completed = profile.completedTours || completedTours;
        setCompletedTours(completed);
        setDisplayCompletedTours(completed);
      }
      setTimeout(() => {
        keepTrackTourProgress();
      }, 3000);
    });
  };
  React.useEffect(() => {
    keepTrackTourProgress();
    getTargetTour();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  const classes = styles();

  const connector = (
    <StepConnector
      classes={{
        line: classes.connectorLine,
      }}
    />
  );

  const isCompleted = (tour) => {
    return (
      displayCompletedTours &&
      tour.id !== activateTour.id &&
      displayCompletedTours.includes(tour.id)
    );
  };

  return (
    <Grid
      container
      style={{
        display: "flex",
        width: "100%",
        height: "100%",
        justifyContent: "center",
      }}
    >
      <Grid item xs={isWindows || isForStartPage() ? 4 : 3} sm={isWindows || isForStartPage() ? 4 : 3} className={classes.stepBox}>
        <Stepper
          className={classes.icon}
          nonLinear
          activeStep={activateStep}
          orientation="vertical"
          connector={connector}
          style={{ padding: "0px", marginTop: "0px" }}
        >
          {selectedTours.map((tour, index) => (
            <Step
              className={classes.description}
              key={tour.title}
              completed={isCompleted(tour)}
            >
              <StepButton
                className={classes.buttonLabel}
                onClick={handleStep(index)}
              >
                {isForStartPage() ? (
                  tour.title
                ) : (
                  <div className={classes.title}>{tour.title}</div>
                )}
              </StepButton>
            </Step>
          ))}
        </Stepper>
      </Grid>
      <Grid item xs={1} sm={1}>
        <Divider
          flexItem
          orientation="vertical"
          style={{
            height: "294px",
            alignSelf: "center",
            marginLeft: "22px",
            marginRight: "32px",
          }}
        />
      </Grid>
      <Grid item xs={7} sm={7}>
        <StepDescription
          isOpenedProject={isOpenedProject}
          completedTours={completedTours}
          activateTour={activateTour}
        />
      </Grid>
    </Grid>
  );
};
