import KatalonEndpoints from '../variables/KatalonEndpoints';
import KatalonInfo from '../variables/KatalonInfo';
import axios from 'axios';
import KatalonTestOpsProjectInfo from '../variables/KatalonTestOpsProjectInfo';

export default class KSService {

  static async fetchContent(url) {
    let data = await axios.get(`${KatalonEndpoints.KatalonContent}?url=${encodeURIComponent(url)}`);
    return data.data;
  }

  static async fetchInfo(info, extraParams) {
    const randomInt = Math.round(Math.random() * 100000000000000);
    let data = await axios.get(`${KatalonEndpoints.KatalonInfo}?t=${randomInt}&info=${info}`);
    return data.data;
  }

  static async fetchTestOpsProjectInfo(info, extraParams) {
    const randomInt = Math.round(Math.random() * 100000000000000);
    let url = `${KatalonEndpoints.KatalonTestOpsProjectInfo}?t=${randomInt}&info=${info}`;
    if (extraParams) {
      url = url + `&${extraParams}`;
    }
    let data = await axios.get(url);
    return data.data;
  }

  static async fetchTrialStatus() {
    return await this.fetchInfo(KatalonInfo.TrialStatus);
  }

  static async fetchVersion() {
    return await this.fetchInfo(KatalonInfo.Version);
  }

  static async fetchIsOnPremActivation() {
    return await this.fetchInfo(KatalonInfo.IS_ON_PREM_ACTIVATION);
  }

  static async fetchLicenseType() {
    return await this.fetchInfo(KatalonInfo.LicenseType);
  }

  static async fetchBuildNumber() {
    return await this.fetchInfo(KatalonInfo.BuildNumber);
  }

  static async fetchUserProfile() {
    return await this.fetchInfo(KatalonInfo.UserProfile);
  }

  static async fetchExpirationDate() {
    return await this.fetchInfo(KatalonInfo.ExpirationDate);
  }

  static async fetchProjectConfiguration() {
    return await this.fetchInfo(KatalonInfo.ProjectConfiguration);
  }

  static async fetch3LatestExecutions(testOpsServerUrl) {
    const extraParams = `testOpsServerUrl=${testOpsServerUrl}`;
    return await this.fetchTestOpsProjectInfo(KatalonTestOpsProjectInfo.get3LatestExecutions, extraParams);
  }

  static async fetchTotalExecutionsStatisticsThisWeek(testOpsServerUrl) {
    const extraParams = `testOpsServerUrl=${testOpsServerUrl}`;
    return await this.fetchTestOpsProjectInfo(KatalonTestOpsProjectInfo.getTotalExecutionsStatisticsThisWeek, extraParams);
  }

  static async fetchProjectStatisticsThisWeek(testOpsServerUrl) {
    const extraParams = `testOpsServerUrl=${testOpsServerUrl}`;
    return await this.fetchTestOpsProjectInfo(KatalonTestOpsProjectInfo.getProjectStatisticsThisWeek, extraParams);
  }

  static async learnMoreAboutCICD() {
    return await this.fetchInfo(KatalonInfo.LearnMoreAboutCICD);
  }
  
  static async fetch6LatestExecutionsTestSuite(testSuitePath, testSuiteName, testOpsServerUrl) {
    const extraParams = `testSuiteName=${testSuiteName}&testSuitePath=${testSuitePath}&testOpsServerUrl=${testOpsServerUrl}`;
    return await this.fetchTestOpsProjectInfo(KatalonTestOpsProjectInfo.get6LatestExecutionsTestSuite, extraParams);
  }

  static async fetchStudioWalkthrough() {
    return await this.fetchInfo(KatalonInfo.StudioWalkthrough);
  }

  static async fetchiSOpenedProject() {
    return await this.fetchInfo(KatalonInfo.IsOpenedProject);
  }
}
